<template>
  <div class="column notes">
      <div class="container">
        <h3 class="is-size-3 note-title">
          {{categoryDescription}}
        </h3>
        <div
          v-for="note in notes" :key="note.name"
        >
          <h5
            class="is-size-5 note-title"
          >
            <a @click="titleClicked(note)">{{ note.Title }}</a>
            <a class="button is-white" @click="editClicked(note)">Edit Note</a>
          </h5>
          <div
            class="note-body"
            v-show="selectedTitle === note.Title"
          >
            <div class="control">
              <textarea class="textarea" readonly placeholder="Normal textarea" v-model="note.Body" rows="20"></textarea>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Notes',
  props: {
    notes: Array,
    categoryDescription: String
  },
  data: function () {
    return {
      selectedTitle: ''
    }
  },
  methods: {
    titleClicked: function (note) {
      if (this.selectedTitle === note.Title) {
        this.selectedTitle = ''
        return
      }
      this.selectedTitle = note.Title
    },
    editClicked: function (note) {
      this.$emit('editNote', note)
    }
  }
}
</script>

<style scoped>
.note-title {
  color: #375EAB;
  padding-top: 10px;
}
.note-body {
  color: black;
}
</style>
